import * as React from "react"

import Layout from "../../components/layout"
import Seo from "../../components/seo"
import ThankYouLetter from "../../components/thankYouLetter"
import '../../pages/about-us/committee_members.css'
import JSONData from "../../data/committee-member-list.json"

const committee_members = () => (
    <Layout page="committee_members">
      <Seo title="Committee Members" description="Committee members, staffs" />
      <div className="main">
        <section className="title-div">
          <strong className="title-committee-members">Committee Members</strong>
        </section>
        <section className="section">
          <div className="columns is-multiline sectionContainer">
            {JSONData.member.map((data) => {
              if (data.name) {
                return (<div className="column is-half columns">
                  <div className="column">
                    <ThankYouLetter src={data.image} alt={data.name} className="picture" />
                  </div>
                  <div className="column">
                    <div className="innerName">{data.name}</div>
                    <div className="innerPosition">{data.position}</div>
                  </div>
                </div>)
              } else {
                return <div className="column is-half" />
              }
            })}
          </div>
        </section>
      </div>
    </Layout>
)

export default committee_members
